import React, { useState, useMemo, useEffect } from 'react';
import { useParams, useLocation } from 'react-router-dom';
import { Table, Card, Container, Row, Col, Form, Button } from 'react-bootstrap';
import { FaDownload, FaSort } from 'react-icons/fa';
import ReactHTMLTableToExcel from 'react-html-table-to-excel';
import { Pagination } from 'antd'; // Import Ant Design Pagination
import "./Userlist.css";
import { getDownstreamListReport, UseReportAll } from '../../../api/login/Login';
import Loader from '../../../common/loader/Loader';

function UserReport() {
    const { userId } = useParams();
    const location = useLocation();
    const [reportData, setReportData] = useState([]);
    const [allReportData, setAllReportData] = useState([]);
    const [totalCount, setTotalCount] = useState(0);
    const [loading, setLoading] = useState(true);
    const [sortConfig, setSortConfig] = useState({ key: null, direction: 'ascending' });
    const [currentPage, setCurrentPage] = useState(1);
    const [itemsPerPage] = useState(10);

    const getCurrentDate = () => {
        const today = new Date();
        return today.toISOString().substr(0, 10);
    };

    const initialFilterState = {
        start_date: getCurrentDate(), 
        end_date: getCurrentDate(), 
        type: '',
        trans_type: '',
        order_id: '',
        txn_id: '',
        sortType: '',
        sortKey: '',
    };

    const [filterValues, setFilterValues] = useState(initialFilterState);
    const [appliedFilters, setAppliedFilters] = useState(initialFilterState);

    useEffect(() => {
        fetchReportData();
    }, [userId, currentPage, itemsPerPage, appliedFilters]);

    const fetchReportData = async () => {
        try {
            setLoading(true);
            const response = await getDownstreamListReport(currentPage - 1, itemsPerPage, userId, appliedFilters);
            if (response.data && response.data.data) {
                setReportData(response.data.data);
                setTotalCount(response.data.totalCount || 0);
            }
        } catch (error) {
            console.error('Error fetching user report:', error);
        } finally {
            setLoading(false);
        }
    };

    const fetchAllReportData = async () => {
        try {
            setLoading(true);
            const response = await UseReportAll(userId);
            if (response.data && response.data.data) {
                setAllReportData(response.data.data);
            }
        } catch (error) {
            console.error('Error fetching all user report data:', error);
        } finally {
            setLoading(false);
        }
    };

    const handleFilterChange = (event) => {
        const { name, value } = event.target;
        setFilterValues(prevState => ({
            ...prevState,
            [name]: typeof value === 'string' ? value.trim() : value
        }));
    };

    const handleSearch = () => {
        setAppliedFilters(filterValues);
        setCurrentPage(1);
    };

    const handleReset = () => {
        setFilterValues({
            start_date: '', 
            end_date: '', 
        });
        setAppliedFilters({
            start_date: '', 
            end_date: '', 
        });
        setCurrentPage(1);
    };

    const requestSort = (key) => {
        let direction = 'ascending';
        if (sortConfig.key === key && sortConfig.direction === 'ascending') {
            direction = 'descending';
        }
        setSortConfig({ key, direction });
        setAppliedFilters(prevState => ({
            ...prevState,
            sortType: direction === 'ascending' ? 1 : -1,
            sortKey: key
        }));
    };

    const sortedAndFilteredData = useMemo(() => {
        let sortableItems = [...(reportData || [])];
        if (sortConfig.key) {
            sortableItems.sort((a, b) => {
                if (a[sortConfig.key] < b[sortConfig.key]) {
                    return sortConfig.direction === 'ascending' ? -1 : 1;
                }
                if (a[sortConfig.key] > b[sortConfig.key]) {
                    return sortConfig.direction === 'ascending' ? 1 : -1;
                }
                return 0;
            });
        }
        return sortableItems;
    }, [reportData, sortConfig]);

    if (loading) {
        return <div><Loader/></div>;
    }

    const handleExcelDownload = async (e) => {
        if (allReportData.length === 0) {
            e.preventDefault();
            await fetchAllReportData();
        }
    };

    const totalPages = Math.ceil(totalCount / itemsPerPage);

    return (
        <Container fluid className="px-4 PageHeading">
            <h1 className="mt-4 mb-4 ">Transaction Report</h1>
        

            <Card className="mb-4 ur-card">
                <Card.Header className="ur-card-header"></Card.Header>
                <Card.Body className="ur-card-body">
                    <Form>
                        <Row className="mb-3">
                            <Col md={3}>
                                <Form.Group controlId="startDate">
                                    <Form.Label>Start Date</Form.Label>
                                    <Form.Control type="date" name="start_date" max={getCurrentDate()} value={filterValues.start_date} onChange={handleFilterChange} />
                                </Form.Group>
                            </Col>
                            <Col md={3}>
                                <Form.Group controlId="endDate">
                                    <Form.Label>End Date</Form.Label>
                                    <Form.Control type="date" name="end_date" max={getCurrentDate()} value={filterValues.end_date} onChange={handleFilterChange} />
                                </Form.Group>
                            </Col>
                            {/* <Col md={3}>
                                <Form.Group controlId="type">
                                    <Form.Label>Type</Form.Label>
                                    <Form.Control as="select" name="type" value={filterValues.type} onChange={handleFilterChange}>
                                        <option value="">Select Type</option>
                                        <option value="all">All</option>
                                        <option value="credit">Credit</option>
                                        <option value="debit">Debit</option>
                                    </Form.Control>
                                </Form.Group>
                            </Col> */}
                        </Row>
                        <Row className="mb-3">
                            {/* <Col md={3}>
                                <Form.Group controlId="order_id">
                                    <Form.Label>Order Id</Form.Label>
                                    <Form.Control type="text" name="order_id" value={filterValues.order_id} onChange={handleFilterChange} />
                                </Form.Group>
                            </Col>
                            <Col md={3}>
                                <Form.Group controlId="txn_id">
                                    <Form.Label>Txn Id</Form.Label>
                                    <Form.Control type="text" name="txn_id" value={filterValues.txn_id} onChange={handleFilterChange} />
                                </Form.Group>
                            </Col> */}
                           <div className="form-group col-md-6 col-lg-4">
          {/* <label>&nbsp;</label> */}
          <div>
            <button
              type="button"
              className="btn btn-primary mr-3"
              onClick={handleSearch}
            >
              Search
            </button>
            <button
              type="button"
              className="btn btn-light"
              onClick={handleReset}
            >
              Reset
            </button>
          </div>
        </div>
                          
                        </Row>
                    </Form>
                </Card.Body>
            </Card>

            <Card className="mb-4 ur-card">
                <Card.Header className="ur-card-header">
                    <Row className="align-items-center w-100">
                        <Col md={12} className="text-end dataTables_length mb-2">
                            <ReactHTMLTableToExcel
                                id="test-table-xls-button"
                                className="btn btn-success ur-download-btn"
                                table="table-to-xls"
                                filename="user_report"
                                sheet="user_report"
                                // <FaDownload className="me-2" />
                                buttonText={<> Download Excel</>}
                                onClick={handleExcelDownload}
                            />
                        </Col>
                    </Row>
                </Card.Header>
                <Card.Body className="ur-card-body">
                    <Table responsive striped bordered hover id="table-to-xls" className="ur-table">
                        <thead>
                            <tr>
                                <th onClick={() => requestSort('_id')} className="ur-th">S.No <FaSort /></th>
                                <th onClick={() => requestSort('_id')} className="ur-th">Transaction Id <FaSort /></th>
                                <th onClick={() => requestSort('amount')} className="ur-th">Amount <FaSort /></th>
                                <th onClick={() => requestSort('trans_type')} className="ur-th">Transaction Type <FaSort /></th>
                                <th onClick={() => requestSort('createdAt')} className="ur-th">Date <FaSort /></th>
                            </tr>
                        </thead>
                        <tbody>
                            {sortedAndFilteredData.length > 0 ? (
                                sortedAndFilteredData.map((item, index) => (
                                    <tr key={index} className="ur-tr">
                                        <td>{(currentPage - 1) * itemsPerPage + index + 1}</td>
                                        <td>{item?.txn_id}</td>
                                        <td>{item?.amount}</td>
                                        <td>{item?.trans_type}</td>
                                        <td>{item?.createdAt}</td>
                                    </tr>
                                ))
                            ) : (
                                <tr>
                                    <td colSpan="5" className="text-center">No Data Found</td>
                                </tr>
                            )}
                        </tbody>
                    </Table>
                    <div style={{marginTop:'1%', float:'right'}}>
                    <Pagination
                        current={currentPage}
                        total={totalCount}
                        pageSize={itemsPerPage}
                        onChange={page => setCurrentPage(page)}
                        showSizeChanger={false} // Hide the size changer
                    />
                    </div>

<p className="ur-total-count">Total Count: {totalCount}</p>
                </Card.Body>
            </Card>
        </Container>
    );
}

export default UserReport;
